import { Toolbar, Container } from '@material-ui/core';
import ButtonHeader from './ButtonHeader';
import TitleHeader from './TitleHeader';
import MenuHeader from './MenuHeader';
import { headerUseStyles } from './styles';

export default function Header(props) {
  const classes = headerUseStyles();

  const { setParamSearch } = props;

  return (
    <Fragment>
      <TitleHeader title='COVID-19' />
      <Container maxWidth="lg">
        <Toolbar component="nav" variant="dense" className={classes.toolbarSecondary}>
          <ButtonHeader setParamSearch={setParamSearch} search={'covid-19'} title='Principais noticias' />
          <ButtonHeader setParamSearch={setParamSearch} search={'noticias internacionais sobre covid-19'} title='Mundo' />
          <MenuHeader setParamSearch={setParamSearch} title='Regiões' />
        </Toolbar>
      </Container>
    </Fragment>
  );  
}
